.main{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ded4d4;
    position: relative;
}

body{
    margin: 0;
}
.snakeBoard{
    width: 416px;
    height: 416px;
    display: grid;
    grid-template-columns: repeat(8,1fr);
    background: transparent;
}
.type1{
    width: 50px;
    height: 50px;
    background: #71ad55;
}
.type2{
    width: 50px;
    height: 50px;
    background: #96f33e;
}
.cubes{
    width: 50px;
    height: 50px;
    border: 1px solid white;
}
.food{
    width: 50px;
    height: 50px;
    background: url("../image/food.png");
    background-size: 100% 100%;
}
.snake{
    width: 50px;
    height: 50px;
    border-radius: 17px;
    border: none;
    background: linear-gradient(180deg,#A0C432 50%,#779226 50%);
    background-size: 100% 100%;
}
.snakeHead{
    width: 50px;
    height: 50px;
    border: none;
    background: url("../image/snakeHead.png");
    background-size: cover;

}


.status{
    width: 300px;
    height: 200px;
    background: #30d58b;
    border-top-left-radius: 70px;
    border-bottom-right-radius: 70px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: bold;
    font-size: 20px;
    position: absolute;
    font-family: 'Acme', sans-serif;
}


#icon{
    font-size: 40px;
    cursor: pointer;
}
#move,#music,#gameOver,#food{
    display: none;
}
.volume{
    width: fit-content;
    height: fit-content;
    color: #71ad55;
    font-size: 40px;
    cursor: pointer;
    position: absolute;
    top: 40px;
}


#column{
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}
.flr{
    width: 100%;
    height: fit-content;
    display: flex;
    align-items: center;
    margin-top: 20px;
    justify-content: space-between;
}
#mk{
    width: 23%;
    height: 50px;
    background: #71ad55;
    border: 0;
    cursor: pointer;
    color: white;
    font-size: 15px;
}
#mk1{
    width: 23%;
    height: 50px;
    background: #96f33e;
    border: 0;
    cursor: pointer;
    color: white;
    font-size: 15px;
}


@media only screen and (max-width: 576px){

    .snakeBoard{
        width: 280px;
        height: 280px;
        display: grid;
        grid-template-columns: repeat(6,1fr);
        background: transparent;
    }
    .type1{
        width: 45px;
        height: 45px;
        background: #71ad55;
    }
    .type2{
        width: 45px;
        height: 45px;
        background: #96f33e;
    }
    .cubes{
        width: 45px;
        height: 45px;
        border: 1px solid white;
    }

    .food{
        width: 45px;
        height: 45px;
        background: url("../image/food.png");
        background-size: 100% 100%;
    }
    .snake{
        width: 45px;
        height: 45px;
        border-radius: 17px;
        border: none;
        background: linear-gradient(180deg,#A0C432 50%,#779226 50%);
        background-size: 100% 100%;
    }

    .snakeHead{
        width: 45px;
        height: 45px;
        border: none;
        background: url("../image/snakeHead.png");
        background-size: cover;

    }

    .flr{
        width: 100%;
        height: fit-content;
        display: flex;
        align-items: center;
        margin-top: 10px;
        justify-content: space-between;
    }
    #mk{
        width: 23%;
        height: 40px;
        background: #71ad55;
        border: 0;
        cursor: pointer;
        color: white;
        font-size: 15px;
        margin: 0;
    }
    #mk1{
        width: 23%;
        height: 40px;
        background: #96f33e;
        border: 0;
        cursor: pointer;
        color: white;
        font-size: 15px;
        margin: 0;
    }



    .status{
        width: 250px;
        height: 150px;
        background: #30d58b;
        border-top-left-radius: 50px;
        border-bottom-right-radius: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;
        font-weight: bold;
        font-size: 14px;
        position: absolute;
        font-family: 'Acme', sans-serif;
    }
    #icon{
        font-size: 30px;
    }

    .volume{
        width: fit-content;
        height: fit-content;
        color: #71ad55;
        font-size: 30px;
        cursor: pointer;
        position: absolute;
        top: 80px;
    }











}



